const onStyle = {
  "background-color": "blue"
};

const steps = step => {
  const steps = [...Array(8)];
  return steps.map((_, i) => {
    console.log(step == i);
    return (
      <div className="column" style={step == i ? onStyle : {}}>
        <input type="checkbox" />
      </div>
    );
  });
};

const Track = props => <div className="columns">{steps()}</div>;

export default Track;
